

.chat-sec {
	width: 100%;
	height: 16.7rem;
	position: fixed;
	bottom: 0;
	left: 0;
	z-index: 200;
	&__icons-bar {
		display: flex;
		justify-content: flex-end;
		align-items: center;
		z-index: 3;
		height: 100%;
		width: 100%;
		position: absolute;
		bottom: 0;
		left: 0;
		transition: 0.5s ease-out; }

	&__open-btn {
		position: relative;
		width: 50%;
		height: 100%;
		display: flex;
		align-items: center;
		padding-left: 8.5rem;
		cursor: pointer;
		transition: 0.5s; }

	&__chat-ic {
		position: absolute;
		height: 2.2rem;
		width: 2.2rem;
		transition: 1s;
		&--white {
			opacity: 1; }
		&--blue {
			opacity: 0; } }

	&__chat-bar {
		z-index: 2;
		position: absolute;
		bottom: 0;
		right: 0;
		width: 100%;
		height: 16.7rem; }

	&__empt-chat {
		position: absolute;
		right: 0;
		bottom: 0;
		width: 50%;
		height: 100%;
		background: $blue;
		transition: 0.5s;
		z-index: 1;
 }		//display: none

	&__chat {
		position: absolute;
		bottom: -200%;
		right: 0;
		height: 31.8rem;
		width: 50%;
		background: $blue; } }

.chat {
	overflow: hidden;
	transition: bottom 1s;
	&__header {
		display: flex;
		align-items: center;
		padding: 3.9rem 8.6rem 0 7.8rem;
		justify-content: space-between;
		position: absolute;
		//top: 0
		top: 100%;
		left: 0;
		width: 100%;
		transition: top 1s 0.4s; }

	&__title {
		@include f-bold;
		color: white;
		font-weight: bold;
		font-size: 2.4rem;
		line-height: 2.9rem; }

	&__tip {
		color: rgba(255, 255, 255, 0.5);
		font-size: 1.6rem;
		line-height: 4rem;
		strong {
			@include f-bold;
			font-weight: bold; } }

	&__text-field {
		position: absolute;
		//bottom: 14.1rem
		bottom: -100%;
		left: 7.8rem;
		width: 54.1rem;
		@include f-regular;
		font-weight: normal;
		padding: 1.8rem 0.5rem;
		font-size: 1.6rem;
		line-height: 1.9rem;
		color: white;
		background: transparent;
		border: none;
		border-bottom: 2px solid rgba(255, 255, 255, 0.1);
		transition: bottom 1s 0.2s;
		&:focus {
			outline: none; } }

	&__close-ic {
		position: absolute;
		//bottom: 6.5rem
		bottom: -100%;
		left: 7.8rem;
		cursor: pointer;
		transition: bottom 0.5s; }


	&__feedback-content {
		position: absolute;
		top: 0;
		left: 100%;
		width: 100%;
		height: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
		transition: left 0.5s 0.8s, top 1s 2.8s;
		&.anim {
			left: 0;
			top: 100%; } } }


// chat shows up when clicking on chat icon
// and hides down when clicking on close chat icon

.chat.open {
	bottom: 0; }

.chat__main-content.show {
	.chat {
		&__header {
			top: 0; }
		&__text-field {
			bottom: 14.1rem; }
		&__close-ic {
			bottom: 6.5rem; } } }

// chat icon changing its place when scrolling a page
.chat-sec.aside {
	.chat-sec {
		&__open-btn {
			width: 2.2rem;
			height: 2.2rem;
			padding: 0;
			margin-right: 8rem; }
		&__chat-ic {
			&--white {
				opacity: 0; }
			&--blue {
				opacity: 1; } }

		&__empt-chat {
			bottom: -100%; } } }
