@import 'colors';
@import 'fonts';

* {
	box-sizing: border-box; }

html {
	font-size: 62.5%; }
body {
	@include f-regular;
	color: $black; }
p, h1, h2 {
	margin: 0;
	color: $black; }

h1, h2 {
	@include f-bold; }

.wrap {
	position: relative;
	width: 100%;
	height: 100%; }

.border {
	border: 1px solid black;
	&-r {
		border: 1px solid red; } }

.tip-target {
	position: relative;
	overflow: visible; }

.tip {
	position: absolute;
	display: block; }

.glob-wrap {
	display: flex;
	flex-direction: column;
	align-items: center; }

.section {
	position: relative;
	padding: 6rem 3.2rem;
	overflow: hidden;
	width: 100%;
	max-width: 160rem;
	&__title {
		@include f-bold;
		color: $black;
		font-weight: bold;
		font-size: 6rem;
		line-height: 8rem;
		width: min-content;
		display: inline-flex;
		align-items: flex-end; }
	&__chat-ic {
		position: absolute; } }

.container {
	display: flex;
	justify-content: center;
	//border: 1px solid red
	&__row {
		width: max-content;
 } }		//border: 1px solid black



.spec-border, .spec-border-t1 {
	border: 1rem solid transparent;
	border-image: url('../img/spec-border-square.svg') 30 round;
	border-image-outset: 0.6rem;
	box-shadow: 0 0 0 2px $black;
	position: relative;
	&::before {
		content: '';
		display: block;
		height: 2.2rem;
		width: 2.2rem;
		background: url("../img/mouse-icon.svg");
		background-size: cover;
		position: absolute; } }

.spec-border {
	&::before {
		bottom: -3rem;
		right: -3rem; } }

@import 'sections/header';
@import 'sections/chat';
@import 'sections/portfolio';
@import 'sections/cv';
@import 'sections/principles';
@import 'sections/lets-talk';
@import 'sections/footer';

@import 'elements/navbar';

.mb-0 {
	margin-bottom: 0; }

#rem {
	width: 1rem; }


